import React, { useRef, useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navbar from "../../components/Navbar";
import Footer from "../../components/Footer";
import Inicio from "./Inicio";
import Presentacion from "./Presentacion";
import Propuestas from "./Propuestas";
import Agenda from "./Agenda";
import Participa from "./Participa";
import Divider from "../../components/Divider";

function LandingPage() {
  const nodeRef = useRef(null);
  const [show, setShow] = useState(false);
  const location = useLocation(); //  Usa React Router, no la global

  useEffect(() => {
    if (location.hash) {
      const element = document.getElementById(location.hash.replace("#", ""));
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
      }
    }
  }, [location]);

  useEffect(() => {
    setShow(true);
  }, []);

  return (
    <div className="bg-cyan-600">
      <Helmet>
        <title>
          ngeles Gallego | Candidata a Rectora de la Universidad de Sevilla
        </title>
        <meta
          name="description"
          content="Soy ngeles Gallego, candidata al Rectorado de la Universidad de Sevilla. Conoce mis propuestas para hacer juntos una nueva universidad. La Universidad que viene la haces t. Otra forma de gobernar la universidad es posible. La US la haces t."
        />
        <meta
          name="keywords"
          content="ngeles Gallego, ngeles gallego, ngeles Gallego Rectora, rectora universidad de Sevilla, candidata rectora US, elecciones rectorado universidad de Sevilla, universidad de Sevilla, universidad participativa, universidad tica, universidad inclusiva, proyecto participativo universidad"
        />
      </Helmet>
      <CSSTransition
        in={show}
        timeout={300}
        classNames="fade"
        unmountOnExit
        nodeRef={nodeRef}
      >
        <div ref={nodeRef} className="font-serif text-gray-900">
          <Navbar />
          <main>
            <section id="inicio" className="scroll-mt-48">
              <Inicio />
            </section>
            <Divider height="96px" />
            <section id="presentacion" className="scroll-mt-48">
              <Presentacion />
            </section>
            <Divider height="96px" />
            <section id="propuestas" className="scroll-mt-48">
              <Propuestas />
            </section>
            <Divider height="96px" />
            <section id="agenda" className="scroll-mt-48">
              <Agenda />
            </section>
            <Divider height="96px" />
            <section id="participa" className="scroll-mt-48">
              <Participa />
            </section>
          </main>
          <Footer />
        </div>
      </CSSTransition>
    </div>
  );
}

export default LandingPage;