/*eslint-disable*/module.exports={messages:JSON.parse("{\"mVMnD2\":[\" CONOCE NUESTRA PROPUESTA \"],\"HK5Fh0\":[\"<0>Porque otra forma de gobernar la universidad es posible.</0> El estilo de gobierno de la Universidad de Sevilla puede y debe mejorarse. Voy a dirigir la Universidad de una forma ms gil y cercana. Se puede hacer ms con menos, sobre todos menos cargos. Me comprometo a una gestin eficaz, eficiente y transparente de los recursos.\"],\"PjPs26\":[\"<0>Porque implantar un sistema de cogobierno con los centros y una verdadera descentralizacin.</0> Las Facultades, Escuelas, Departamentos e Institutos desarrollan la actividad bsica de la Universidad y tendrn una gran autonoma para hacerlo, pero con control y rendicin de cuentas.\"],\"zZqEr2\":[\"<0>Porque la rectora te va a escuchar (y a responder).</0> La Rectora debe fomentar el dilogo a todos los niveles y no imponer su monlogo. Debe escuchar y atender a toda la comunidad universitaria.\"],\"1CJe65\":[\"<0>Porque el equipo sers t. La Rectora quiere contar con todos para hacer la Universidad que viene.</0> No hay cargos ni equipo porque todava hay que acordar con todos cmo queremos que sea la estructura adelgazada de la Universidad.\"],\"VCwa1w\":[\"<0>Porque todos somos la universidad de Sevilla.</0> Hay que aprovechar el talento y la experiencia de toda la comunidad universitaria y empezar a gobernar de abajo arriba y de la periferia al centro. Porque las decisiones las tomarn quienes sepan. Hay que hacer que las decisiones se tomen a travs de acuerdos y con el mximo nivel posible de consenso y que adems participen en ellas quienes realmente conozcan cada cuestin y aquellos que se vean afectados.\"],\"yktLOT\":[\"<0>LA UNIVERSIDAD QUE VIENE LA HACES T</0>\"],\"fXxqy3\":[\"Una carta a la comunidad universitaria\"],\"447C14\":[\"Una estudiante con dificultades e incertidumbres hasta que me gradu en Medicina hace dos aos. Una profesora que vive con la ilusin del primer da que un alumno aprenda algo nuevo, y que se agobia con los cambios en los procedimientos y en las aplicaciones informticas. Una investigadora que se sorprende y se entusiasma con cada descubrimiento y que vive con miedo a que una liquidacin de un proyecto est mal hecha. Una trabajadora de la Universidad de Sevilla que no ha tenido padrinos. Me preocupa la gente como t y como yo. No tengo ningn inters poltico ni de otro tipo que defender. Estoy orgullosa de pertenecer a la Universidad de Sevilla y de sentirme una parte de ella. Ser una Rectora diferente, ser como t.\"],\"+Xzu3k\":[\"A student who faced difficulties and uncertainties until I graduated in Medicine two years ago...\"],\"5WT06W\":[\"SOBRE M\"],\"sGxHeV\":[\"Todos los derechos reservados\"],\"xhQj64\":[\"Aunque todava soy joven, llevo casi 30 aos en la Universidad de Sevilla. Soy catedrtica de Organizacin de Empresas. Licenciada en Ciencias Econmicas y Empresariales, graduada en Medicina y doctora en Administracin y ocupado tambin de la gestin universitaria. Y pienso que mi trabajo, el de profesora e investigadora, es el mejor del mundo y una de las mejores cosas que me han pasado en la vida.\"],\"p0KEeR\":[\"Y soy como t.\"],\"0bAvzz\":[\"Y es que otro estilo de gobierno de la Universidad de Sevilla es posible. Otra forma de ser Rectora es posible. Otra forma de hablarnos, de escucharnos y de decidir juntos es posible. T lo puedes hacer posible. Te necesito para ser Rectora, pero cuando realmente te voy a necesitar es cuando sea Rectora. Si todos lo hacemos (mi equipo y yo seremos los primeros), conseguiremos una Universidad mejor, donde nos sintamos mejor, donde hagamos mejor nuestro trabajo (el que sea) y donde encontremos el mejor sitio para aprender, para titularnos, para investigar y para trabajar.\"],\"TV+uAx\":[\"Because another style of governing the University of Seville is possible. Another way of being Rector is possible. Another way of speaking to each other, listening to each other, and making decisions together is possible. You can make it possible. I need you to become Rector, but when I will truly need you is once I am Rector. If we all do thisand my team and I will be the first to do sowe will achieve a better University, where we feel better, where we do our work (whatever it may be) better, and where we find the best place to learn, to graduate, to research, and to work.<0/><1/>We will not achieve it on the first day, but we will have six years to try together. With you, of course it is possible. From this moment, I consider you part of my team.\"],\"fBua1t\":[\"Biografa\"],\"21BIFi\":[\"BIOGRAFA\"],\"ZijJqC\":[\"CANDIDATA A RECTORA\"],\"jfC/xh\":[\"Contacto\"],\"N/+7yd\":[\"DESCARGAR CV\"],\"i0qMbr\":[\"Inicio\"],\"lvfaxv\":[\"INICIO\"],\"GnLp4l\":[\"Soy <0>NGELES GALLEGO</0> y quiero ser tu <1>RECTORA</1>.\"],\"lhuUrx\":[\"I am <0>NGELES GALLEGO</0> and I want to be your <1>RECTOR</1>.<2/><3/>And I am like you.<4/><5/>A student who faced difficulties and uncertainties until I graduated in Medicine two years ago. A professor who lives with the excitement of seeing a student learn something new for the first time, and who feels overwhelmed by changes in procedures and IT systems. A researcher who is amazed and thrilled with every discovery, yet afraid a project report might be submitted incorrectly. A worker at the University of Seville who has had no patrons. My only concern is people like you and me. I have no political or other interests to defend. I am proud to belong to and feel part of the University of Seville. I will be a different kind of Rector; I will be like you.<6/> <7/>Although I am still young, I have spent nearly 30 years at the University of Seville. I am a Professor of Business Organization. I hold a degree in Economics and Business Studies, a degree in Medicine, and a PhD in Management and Business Administration. I have management experience in the banking sector and have also been involved in university administration. And I believe my work as a professor and researcher is the best job in the world, and one of the greatest things that has happened to me in life.\"],\"u97/c7\":[\"I am <0>NGELES GALLEGO</0> and I want to be your <1>RECTOR</1>.<2/><3/>And I am like you.<4/><5/>A student who faced difficulties and uncertainties until I graduated in Medicine two years ago. A professor who lives with the excitement of seeing a student learn something new for the first time, and who feels overwhelmed by changes in procedures and IT systems. A researcher who is amazed and thrilled with every discovery, yet afraid a project report might be submitted incorrectly. A worker at the University of Seville who has had no patrons. My only concern is people like you and me. I have no political or other interests to defend. I am proud to belong to and feel part of the University of Seville. I will be a different kind of Rector; I will be like you.<6/><7/>Although I am still young, I have spent nearly 30 years at the University of Seville. I am a Professor of Business Organization. I hold a degree in Economics and Business Studies, a degree in Medicine, and a PhD in Management and Business Administration. I have management experience in the banking sector and have also been involved in university administration. And I believe my work as a professor and researcher is the best job in the world, and one of the greatest things that has happened to me in life.\"],\"qdJ1m1\":[\"I am <0>NGGALLEGO</0> and I want to be your <1>RECTOR</1>.\"],\"ljhnNe\":[\"Doy un paso adelante con la fuerza que aportan todas las personas con las que comparto esa necesidad de construir la Universidad que este momento demanda, y con el claro objetivo de liderar una nueva US. Este es un proyecto que tenemos que construir juntos e invito a cualquier miembro de la comunidad universitaria a participar en l. Las puertas de mi candidatura estn abiertas para todos porque <0>La Universidad que viene la haces t</0>.\"],\"lbjoFH\":[\"Quiero una Universidad al servicio de las personas. Hay que cambiar la cultura de la institucin para que la salud, el respeto, la igualdad y la tica estn en el quehacer diario. Voy a pelear para que la gente de la Universidad de Sevilla se sienta feliz y comprometida con ella. Buscar espacios para los afectos y las risas. Y nos lo tomaremos todo (o casi) con sentido del humor. Y espero contar contigo porque la Universidad que viene la haces t.\"],\"65y/l6\":[\"QUIERO SER TU RECTORA\"],\"398dbL\":[\"Quiero ser tu Rectora porque un conjunto de personas de distintos sectores me ha sugerido que lo hiciera. He contado con un grupo de gente que est dispuesta a ayudarme, despus de escuchar a miembros de los distintos colectivos.Los que estamos en esta candidatura somos gente normal, sin aspiraciones a ocupar cargos, con la generosidad que supone aadir trabajo, tiempo y esfuerzos extra para el bien de nuestra Universidad y de su comunidad. Somos gente como t. Y, como t, trabajamos cada da en la Universidad y hacemos que funcione.\"],\"u+/OOb\":[\"Presentacion\"],\"VuZrNd\":[\"PRESENTACIN\"],\"biwvFm\":[\"PARTICIPA\"],\"O0Y+Sj\":[\"Quiero mantenerme informado/a.\"],\"5+jQOp\":[\"LA UNIVERSIDAD QUE VIENE, <0/> LA HACES T\"],\"4s8rMJ\":[\"MIRA LO QUE PODEMOS CONSEGUIR JUNTOS\"],\"9hajs/\":[\"CONOCE A LA CANDIDATA\"],\"amn0yp\":[\"Motivacin\"],\"3oUXzh\":[\"MI MOTIVACIN\"],\"kylqyr\":[\"Agenda y Noticias\"],\"9YliER\":[\"AGENDA Y NOTICIAS\"],\"CovYpP\":[\"Ahora es el momento de hacer todo esto. Hay que aprovechar la oportunidad de estas elecciones con un nuevo formato para implantar otro estilo de relaciones y de gobierno en nuestra Universidad.\"],\"hESo/4\":[\"O EXPLORA\"],\"/IX/7x\":[\"Otro\"],\"kN6Wkt\":[\"NUESTRO PROGRAMA\"],\"gaCyZ3\":[\"PARTICIPA\"],\"QftTdJ\":[\"PDI\"],\"iCdVLj\":[\"PRESENTATION\"],\"6g1gi0\":[\"Propuestas\"],\"v1z8kN\":[\"PROPUESTAS\"],\"Ce92BF\":[\"PTGAS\"],\"72boRg\":[\"QUIERO SER TU RECTORA\"],\"gR3ouv\":[\"LEER MS\"],\"8mDLgw\":[\"VOLVER\"],\"WbozdU\":[\"AGENDA\"],\"3+MXzC\":[\"Selecciona un perfil\"],\"SUrLL9\":[\"Selecciona un perfil para filtrar las propuestas segn dese.\"],\"JlFcis\":[\"Enviar\"],\"ZR57vN\":[\"Atentamente,\"],\"51mRUj\":[\"La sociedad se encuentra en un momento donde priman las incertidumbres y se pone a menudo en tela de juicio el papel de la ciencia para afrontarlas. Por otra parte, el cuestionamiento al que asistimos de lo pblico se une al hecho de que las universidades privadas empiezan a comernos terreno. Debemos despertarnos e intentar dar una respuesta gil y rpida a los problemas que surjan.\"],\"EVV8/3\":[\"Soy <0>NGELES GALLEGO</0> y quiero ser tu <1>RECTORA</1>.<2/><3/>Y soy como t.<4/><5/>Una estudiante con las dificultades y las incertidumbres hasta que me gradu en Medicina hace dos aos. Una profesora que vive con la ilusin del primer da que un alumno aprenda algo nuevo y que se agobia con los cambios en los procedimientos y en las aplicaciones informticas. Una investigadora que se sorprende y se entusiasma con cada descubrimiento y vive con miedo a que una liquidacin de un proyecto est mal hecha. Una trabajadora de la Universidad de Sevilla que no ha tenido padrinos. Solo me preocupa la gente como t y como yo.<6/><7/>No tengo ningn inters poltico ni de otro tipo que defender. Estoy orgullosa de pertenecer y de sentirme una parte de la Universidad de Sevilla. Ser una Rectora diferente, ser como t. Aunque todava soy joven, llevo casi 30 aos en la Universidad de Sevilla. Soy Catedrtica de Organizacin de Empresas. Licenciada en Ciencias Econmicas y Empresariales, Graduada en Medicina y Doctora en Administracin y Direccin. He tenido experiencia directiva en el sector bancario y me he ocupado tambin de la gestin universitaria. Y pienso que mi trabajo, el de profesora e investigadora, es el mejor del mundo y una de las mejores cosas que me han pasado en la vida.\"],\"a0qEkS\":[\"Estudiante\"],\"WKOJ7q\":[\"ESTUDIANTE\"],\"gjANha\":[\"El Rectorado, adems de la figura de la Rectora, que debe dirigir y responsabilizarse de toda la organizacin, es bsicamente donde se concentran todos los servicios de apoyo a los dos servicios bsicos que presta la Universidad: la <0>investigacin y la docencia</0>. Algunos lo llaman servicios centrales. Pero mi idea es una Universidad de Sevilla ms descentralizada. Esos servicios centrales deben redimensionarse para cumplir adecuadamente sus funciones en cada momento. Esto implica una importante dosis de flexibilidad. La estructura del gobierno de la Universidad de Sevilla durante mi mandato ser (re)diseada con regularidad para hacer frente a los cambios, a las demandas del entorno y a los retos estratgicos que entre todos nos propongamos. Con ello, pretendemos ofrecer certidumbres y proteger lo pblico.\"],\"R85k7N\":[\"The Rectorate, in addition to the role of the Rector, who must lead and take responsibility for the entire organization, is essentially where all the support services for the Universitys two core missionsresearch and teachingare concentrated. Some call them central services. But my vision is of a more decentralized University of Seville. These central services must be resized and adapted in order to properly fulfill their functions at any given time. This requires a significant degree of flexibility. The structure of the University of Sevilles governance during my term will be (re)designed regularly to respond to change, to the demands of our environment, and to the strategic challenges we set for ourselves together. In this way, we aim to provide certainty and to safeguard the public good.\"],\"cmAJeO\":[\"THE UNIVERSITY TO COME, <0/> IS YOURS TO MAKE\"],\"OwDkQv\":[\"THE UNIVERSITY TO COME,<0/>IS YOURS TO MAKE\"],\"dHzWRg\":[\"LA UNIVERSIDAD QUE VIENE...<0/>LA HACES T\"],\"6BMzyL\":[\"LA UNIVERSIDAD QUE MERECEMOS<0/>EMPIEZA CONTIGO\"],\"MdcSRc\":[\"No lo vamos a lograr el primer da, pero tendremos seis aos para intentarlo juntos. Contigo claro que es posible. Desde este momento te considero miembro de mi equipo.\"],\"KBB8H+\":[\"Because another style of governing the University of Seville is possible. Another way of being Rector is possible. Another way of speaking to each other, listening to each other, and making decisions together is possible. You can make it possible. I need you to become Rector, but when I will truly need you is once I am Rector. If we all do thisand my team and I will be the first to do sowe will achieve a better University, where we feel better, where we do our work (whatever it may be) better, and where we find the best place to learn, to graduate, to research, and to work.<0/><1/>We will not achieve it on the first day, but we will have six years to try together. With you, of course it is possible. From this moment, I consider you part of my team.\"],\"iE1Bur\":[\"Su contribucin enviado correctamente!\"],\"M89Rvp\":[\"Quiero una Universidad al servicio de las personas. Hay que cambiar la cultura de la institucin para que la salud, el respeto, la igualdad y la tica estn en el quehacer diario. Voy a pelear para que la gente de la Universidad de Sevilla se sienta feliz y comprometida con ella. Buscar espacios para los afectos y las risas. Y nos lo tomaremos todo (o casi) con sentido del humor. Y espero contar contigo porque la Universidad que viene la haces t.\"],\"KlFZri\":[\"El Rectorado, adems de la figura de la Rectora, que debe dirigir y responsabilizarse de toda la organizacin, es bsicamente donde se concentran todos los servicios de apoyo a los dos servicios bsicos que presta la Universidad: la investigacin y la docencia. Algunos lo llaman servicios centrales. Pero mi idea es una Universidad de Sevilla ms descentralizada. Esos servicios centrales deben redimensionarse para cumplir adecuadamente sus funciones en cada momento. Esto implica una importante dosis de flexibilidad. La estructura del gobierno de la Universidad de Sevilla durante mi mandato ser (re)diseada con regularidad para hacer frente a los cambios, a las demandas del entorno y a los retos estratgicos que entre todos nos propongamos. Con ello, pretendemos ofrecer certidumbres y proteger lo pblico.\"]}")};